<template>
  <dl>
    <template v-if="showName && (showAll || item.name != null)">
      <dt>{{ $t('name') }}</dt>
      <dd>{{ item.name }}</dd>
    </template>
    <template v-if="showAll || item.car != null">
      <dt>{{ $t('car') }}</dt>
      <dd>{{ item.car ? formatCarNumber(item.car.name) : $t('N/A') }}</dd>
    </template>
    <template v-if="showAll || item.date != null">
      <dt>{{ $t('date') }}</dt>
      <dd>{{ $d(new Date(item.defectDate), 'date-time-weekday') }}</dd>
    </template>
    <template v-if="showAll || item.description != null">
      <dt>{{ $t('description') }}</dt>
      <dd>{{ item.description }}</dd>
    </template>
  </dl>
</template>

<script>
import base from './base'
import { formatCarNumber } from '@/common'
export default {
  mixins: [base],
  data: () => ({
    formatCarNumber
  })
}
</script>
