<template>
  <Scroller :loading="loading" class="py-0" @load="load()">
    <v-flex xs12>
      <v-timeline :dense="$vuetify.breakpoint.mdAndDown" align-top class="mx-3">
        <v-timeline-item
          v-for="({ prev, prevUniq, next, nextUniq, user, date, type }, i) in items"
          :key="'timeline_item_' + i"
          color="primary"
          fill-dot
          :icon="(prev && next && 'mdi-pencil') || (prev && 'mdi-delete') || (next && 'mdi-plus')"
        >
          <v-card color="primary" dark>
            <v-card-title>
              <div class="fw mb-3 title text-xs-center" v-text="$t(`item-types.${type}`)" />
              <span class="hw text-xs-left" v-text="`${user.firstName} ${user.lastName}`" />
              <span class="hw text-xs-right" v-text="`${$d(new Date(date), 'date-time-weekday')}`" />
            </v-card-title>
            <v-card-text class="white text--primary pt-0 px-0">
              <v-layout wrap>
                <template v-if="prev ? !next : next">
                  <v-flex xs12 mx-3>
                    <h5 class="headline py-4" v-text="$t((next && 'created') || (prev && 'deleted'))" />
                    <div :is="`${type}DL`" :item="next || prev" :show-name="true" :show-all="false" />
                  </v-flex>
                </template>
                <template v-else>
                  <span class="hw px-3">
                    <h5 class="headline py-4" v-text="$t('before-change')" />
                    <div :is="`${type}DL`" :item="opened[i] ? prev : prevUniq" :show-name="true" :show-all="false" />
                  </span>
                  <span class="hw px-3">
                    <h5 class="headline py-4" v-text="$t('after-change')" />
                    <div :is="`${type}DL`" :item="opened[i] ? next : nextUniq" :show-name="true" :show-all="false" />
                  </span>
                  <v-flex v-if="Object.keys(prev).length !== Object.keys(prevUniq).length + 1" xs12 mt-3 mx-3>
                    <v-btn flat color="primary" class="fw ma-0" @click="$set(opened, i, !opened[i])">
                      <v-icon>
                        {{ opened[i] ? 'mdi-chevron-up' : 'mdi-chevron-down' }}
                      </v-icon>
                    </v-btn>
                  </v-flex>
                </template>
              </v-layout>
            </v-card-text>
          </v-card>
        </v-timeline-item>
      </v-timeline>
    </v-flex>
  </Scroller>
</template>

<script>
import Scroller from '@/components/Scroller'

import { getHistory } from '@/axios'

import AirBreakDL from '@/components/itemDL/AirBreak'
import CarDL from '@/components/itemDL/Car'
import HandBreakDL from '@/components/itemDL/HandBreak'
import SerieDL from '@/components/itemDL/Serie'
import SubjectDL from '@/components/itemDL/Subject'
import UserDL from '@/components/itemDL/User'

export default {
  components: {
    Scroller,
    // Used dynamically by :is.
    /* eslint-disable vue/no-unused-components */
    AirBreakDL,
    CarDL,
    HandBreakDL,
    SerieDL,
    SubjectDL,
    UserDL
    /* eslint-enable vue/no-unused-components */
  },
  data: () => ({
    items: [],
    loading: false,
    opened: []
  }),
  methods: {
    processItems (items) {
      return items.map(item => {
        const { prev, next } = item
        if (prev && next) {
          const prevUniq = {}
          const nextUniq = {}
          Object.keys(next).forEach(key => {
            if (prev[key] === next[key]) {
              return
            } else if (prev[key] instanceof Object) {
              const idKey = Object.keys(prev[key]).find(key => /Id$/.test(key))
              if (prev[key][idKey] === next[key][idKey]) {
                return
              }
            }

            prevUniq[key] = prev[key]
            nextUniq[key] = next[key]
          })
          return { ...item, prevUniq, nextUniq }
        } else {
          return item
        }
      })
    },
    async load (clear) {
      this.loading = true
      if (clear) {
        this.items = []
      }
      try {
        const items = await getHistory(this.items.length, 10)
        if (items.length) {
          this.items.push(
            ...this.processItems(items)
          )
        }
      } catch (error) {
      } finally {
        this.loading = false
      }
    }
  }
}
</script>

<style scoped>
.hw { min-width: 50%; }
.fw { width: 100%; }
</style>
