<template>
  <v-container v-scroll="scroll" ref="container">
    <v-layout wrap>
      <slot />

      <v-flex :style="{ visibility: loading ? 'visible' : 'hidden' }" xs12 text-xs-center class="pa-5">
        <v-progress-circular :indeterminate="true" color="primary" />
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  props: {
    loading: { type: Boolean, required: true }
  },
  data: () => ({
    lastHeight: null
  }),
  watch: {
    loading (val) {
      if (!val) {
        window.setTimeout(() => {
          if (this.lastHeight !== this.$refs.container.scrollHeight) {
            this.scroll()
          }
        }, 50)
      }
    }
  },
  mounted () {
    this.emitLoad()
    window.addEventListener('resize', this.scroll)
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.scroll)
  },
  methods: {
    scroll () {
      if (
        !this.loading && (
          document.documentElement.offsetHeight - 256 <=
          document.documentElement.scrollTop + window.innerHeight
        )
      ) {
        this.emitLoad()
      }
    },
    emitLoad () {
      this.lastHeight = this.$refs.container.scrollHeight
      this.$emit('load')
    }
  }
}
</script>
