<template>
  <v-app>
    <v-navigation-drawer v-model="drawer" app>
      <v-list>
        <v-list-tile v-for="({text, to}, i) in items" :key="i" :to="to">
          <v-list-tile-action>
            <v-icon>mdi-checkbox-blank</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title v-text="text" />
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>

    <v-toolbar app>
      <v-toolbar-side-icon @click.stop="drawer = !drawer" />

      <v-toolbar-title class="headline text-uppercase">
        <span class="accent--text">KST</span>
        <span class="primary--text font-weight-light">INPRO</span>
      </v-toolbar-title>

      <v-spacer />
    </v-toolbar>

    <v-content>
      <transition name="fade" mode="out-in">
        <router-view />
      </transition>
    </v-content>

    <vue-snotify />
  </v-app>
</template>

<script>
export default {
  name: 'App',
  data: () => ({
    drawer: true,
    rawItems: [
      [
        'home'
      ], [
        'administration',
        'air-breaks',
        'cars',
        'defects',
        'good-groups',
        'hand-breaks',
        'leases',
        'repairs',
        'revisions',
        'series',
        'subjects'
      ], [
        'login',
        'logout'
      ]
    ]
  }),
  computed: {
    isAllowed () {
      return this.$store.getters.isAllowed
    },
    items () {
      return [].concat(
        ...this.rawItems.map(group =>
          group.filter(item =>
            this.isAllowed(item)
          ).map(item => ({
            text: this.$t(item),
            to: { name: item }
          })).sort((a, b) =>
            a.text.localeCompare(b.text)
          )
        )
      )
    }
  }
}
</script>

<style scoped>
.v-list__tile--link:not(.v-list__tile--active) > .v-list__tile__action {
  visibility: hidden;
}
</style>

