<template>
  <Scroller :loading="loading" @load="load()">
    <v-flex v-if="xhrs.searchItems" xs12 py-5>
      <v-text-field
        v-model="search"
        solo
        clearable
        style="max-width: 600px; margin: 0 auto;"
        @keyup.enter="load(true)"
        @click:clear="() => { search = null; load(true) }"
      >
        <v-btn slot="append" flat color="primary" @click="load(true)" v-text="$t('search')" />
      </v-text-field>
    </v-flex>

    <v-flex xs12>
      <v-layout v-if="items.length || loading" row wrap class="item-container">
        <v-card v-for="(item, index) in items" :key="`card_${i18nKey}_${index}`" class="ma-2 item-card" d-flex>
          <v-card-title class="headline">
            <slot :item="item" name="headline" />
          </v-card-title>
          <v-card-text class="text-xs-left">
            <slot :item="item" />
          </v-card-text>
          <v-card-actions v-if="rw || actions.length">
            <v-btn
              v-for="(action, i) in gruppedActions.buttons"
              :key="`card_${i18nKey}_${index}_action_${i}`"
              color="primary"
              flat
              @click="action.exec(items[index])"
              v-text="action.text"
            />
            <v-menu
              v-if="gruppedActions.list.length"
              open-on-hover
              origin="bottom left"
              top
              transition="scale-transition"
            >
              <v-btn slot="activator" flat color="primary" v-text="$t('more')" />
              <v-list>
                <v-list-tile
                  v-for="(action, i) in gruppedActions.list"
                  :key="`card_${i18nKey}_${index}_action_${i}`"
                  color="primary"
                  @click="action.exec(items[index])"
                >
                  <v-list-tile-title @click="action.exec(items[index])" v-text="action.text" />
                </v-list-tile>
              </v-list>
            </v-menu>

            <v-spacer />
            <v-btn v-if="rw" flat color="primary" @click="edit(index)" v-text="$t('edit')" />
            <v-btn v-if="rw" :loading="removing[index]" flat color="primary" @click="remove(index)">
              {{ $t('delete') }}
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-layout>

      <div v-else class="text-xs-center headline">{{ $t('no-items') }}</div>
    </v-flex>

    <slot
      :item="items[index] || createNewItem()"
      :itemCb="updateArray" :show="show"
      :showCb="editShowCb" name="edit"
    />

    <v-btn v-if="ra" color="accent" fixed dark fab bottom right @click="edit(-1)">
      <v-icon>mdi-plus</v-icon>
    </v-btn>
  </Scroller>
</template>

<script>
import Scroller from './Scroller'

export default {
  components: { Scroller },
  props: {
    actions: { type: Array, default: () => [] },
    i18nKey: { type: String, default: '' },
    idProp: { type: String, default: 'id' },
    newItem: { type: Object, default: () => ({}) },
    xhrs: { type: Object, default: () => ({}) }
  },
  data: () => ({
    index: -1,
    itemIds: new Set(),
    items: [],
    loading: false,
    localItems: 0,
    removing: [],
    search: null,
    show: false
  }),
  computed: {
    columns () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': return 1
        case 'sm': return 2
        case 'md': return 3
        case 'lg': return 4
        case 'xl': return 6
        default: return 1
      }
    },
    itemsPerLoad () {
      return 3 * this.columns
    },
    groups () {
      const items = this.items.map((item, index) => ({ item, index }))
      return [...Array(this.columns)].map((_, group) =>
        items.filter((_, i) =>
          i % this.columns === group
        )
      )
    },
    permission () {
      return this.$store.getters.isAllowed(this.$route.name)
    },
    rw () {
      return this.permission === 'rw'
    },
    ra () {
      return this.permission === 'rw' || this.permission === 'ra'
    },
    gruppedActions () {
      const limit = this.rw ? 1 : 3
      const buttonsEnd = this.actions.length <= limit ? limit : limit - 1
      return {
        buttons: this.actions.slice(0, buttonsEnd),
        list: this.actions.slice(buttonsEnd).reverse()
      }
    }
  },
  watch: {
    xhrs () {
      this.load(true)
    }
  },
  methods: {
    showAlert (type, result) {
      this.$snotify[result](this.$t(
        `xhr-feedback.${type}.${result}`,
        { item: this.$t(this.i18nKey) }
      ))
    },
    edit (i) {
      this.index = i
      this.show = true
    },
    async updateArray (item) {
      if (this.index >= 0) {
        this.items.splice(this.index, 1, item)
      } else {
        ++this.localItems
        this.items.push(item)
        this.itemIds.add(item[this.idProp])
      }
    },
    async remove (i) {
      this.$set(this.removing, i, true)

      let confirmed = await this.$confirm(this.$t('confirm-delete'))
      while (confirmed) {
        try {
          const item = this.items[i]
          await this.xhrs.deleteItem(item[this.idProp])
          this.items.splice(i, 1)
          this.showAlert('delete', 'success')
          break
        } catch (error) {
          confirmed = await this.$confirm(this.$t('deleting-failed-do-you-wanna-retry'))
        }
      }

      this.$set(this.removing, i, false)
    },
    async load (clear) {
      this.loading = true
      if (clear) {
        this.items = []
      }
      try {
        const amount = this.items.length - this.localItems
        const items = this.search != null && this.search !== ''
          ? await this.xhrs.searchItems(this.search, amount, this.itemsPerLoad)
          : await this.xhrs.getItems(amount, this.itemsPerLoad)
        if (items.length) {
          this.items.push(...items.filter(item => {
            const id = item[this.idProp]
            if (this.itemIds.has(id)) {
              return false
            } else {
              this.itemIds.add(id)
              return true
            }
          }))
        }
      } catch (error) {
        this.showAlert('load', 'error')
      } finally {
        this.loading = false
      }
    },
    createNewItem () {
      return JSON.parse(JSON.stringify(this.newItem))
    },
    editShowCb (val) {
      this.show = val
    }
  }
}
</script>

<style scoped>
.item-container {
  justify-content: center;
}
.item-card {
  width: 350px;
}
</style>
