function formatCarNumber (carNumber) {
  const n = `${carNumber}`.replace(/\D/g, '')
  return n.length === 12
    ? `${n.slice(0, 2)}\xa0${n.slice(2, 4)}\xa0${n.slice(4, 8)}\xa0${n.slice(8, 11)}\xa0-\xa0${n.slice(11, 12)}`
    : carNumber
}
export { formatCarNumber }

function formatCarNumberRegularSpace (carNumber) {
  return carNumber != null
    ? formatCarNumber(carNumber).replace(/\xa0/g, ' ')
    : carNumber
}
export { formatCarNumberRegularSpace }
