import { build } from './builder'
import { download } from './common'

const {
  search: searchCars,
  get: getCars,
  post: postCar,
  put: putCar,
  delete: deleteCar
} = build('/car', 'carId')

async function getCarDashboard () {
  const { data } = await download({
    method: 'get',
    url: `/car/dashboard`
  })
  return data || []
}

export {
  searchCars, getCars, postCar, putCar, deleteCar,
  getCarDashboard
}
