<template>
  <v-autocomplete
    v-model="select"
    :item-text="itemText"
    :item-value="itemValue"
    :items="processedItems"
    :label="label"
    :loading="loading"
    :rules="rules"
    :search-input.sync="search"
    hide-no-data
    @focus="querySelections(search)"
  />
</template>

<script>
export default {
  props: {
    formatText: { type: Function, default: (v) => v },
    itemText: { type: String, default: '' },
    itemValue: { type: String, default: '' },
    label: { type: String, default: '' },
    rules: { type: Array, default: () => [] },
    searchItems: { type: Function, default: () => [] },
    value: { type: Object, default: null }
  },
  data: () => ({
    console,
    loading: false,
    search: null,
    items: [],
    searchTimeout: null
  }),
  computed: {
    select: {
      get () {
        return (this.value || {})[this.itemValue]
      },
      set (value) {
        const item = this.items.find(item => item[this.itemValue] === value)
        this.$emit('input', item)
      }
    },
    processedItems () {
      return [...(this.value ? [this.value] : []), ...this.items]
        .map(v => ({ ...v, [this.itemText]: this.formatText(v[this.itemText]) }))
    }
  },
  watch: {
    search (val) {
      if (val != null && val !== (this.value || {})[this.itemText]) {
        this.querySelections(val)
      }
    }
  },
  methods: {
    querySelections (val) {
      const v = `${val || ''}`
      window.clearTimeout(this.searchTimeout)
      this.searchTimeout = window.setTimeout(async () => {
        this.searchTimeout = null
        try {
          this.loading = true
          this.items = await this.searchItems(v, 0, 100)
        } catch (error) {
        } finally {
          this.loading = false
        }
      }, 600)
    }
  }
}
</script>
