import { build } from './builder'
import { download } from './common'

const {
  search: searchRevisions,
  get: getRevisions,
  post: postRevision,
  put: putRevision,
  delete: deleteRevision
} = build('/revision', 'revisionId')

async function getCarRevisions (carId, skip, count) {
  if (skip !== 0) {
    // Backend doesn't support paging.
    return []
  }

  const { data } = await download({
    method: 'get',
    url: `/revision/${carId}/revisions`
  })
  return data || []
}

export {
  searchRevisions, getRevisions, postRevision, putRevision, deleteRevision,
  getCarRevisions
}
