<template>
  <v-form ref="form" v-model="valid">
    <DialogWrapper
      :headline="$t('lease')"
      :save-btn="valid"
      :saving="saving"
      :value="show"
      @cancel="cancel"
      @save="save"
    >
      <v-flex xs12>
        <RemoteSelect
          v-model="item.car"
          :label="$t('car')"
          :rules="[rules.required]"
          :search-items="searchCars"
          :format-text="formatCarNumberRegularSpace"
          item-text="name"
          item-value="carId"
        />
      </v-flex>
      <!--
        <v-flex xs12>
          <DateTime v-model="item.startDate" :label="$t('since')" :rules="[rules.required]" />
        </v-flex>
      -->
      <v-flex xs12>
        <DateTime v-model="item.endDate" :label="$t('to')" :rules="[rules.required]" />
      </v-flex>
      <v-flex xs12>
        <RemoteSelect
          v-model="item.subject"
          :label="$t(`switch.lease-target-agent.${item.leaseType || 0}`)"
          :rules="[rules.required]"
          :search-items="searchSubjects"
          item-text="name"
          item-value="subjectId"
        />
      </v-flex>
      <v-flex xs12>
        <v-switch v-model="leaseType" :label="$t(`switch.lease.${item.leaseType || 0}`)" :rules="[rules.required]" />
      </v-flex>
    </DialogWrapper>
  </v-form>
</template>

<script>
import DateTime from '@/components/DateTime'
import DialogMixin from './DialogMixin'
import RemoteSelect from '@/components/RemoteSelect'
import { formatCarNumberRegularSpace } from '@/common'
import { postLease, putLease, searchCars, searchSubjects } from '@/axios'

export default {
  components: { DateTime, RemoteSelect },
  mixins: [DialogMixin],
  data: () => ({
    idProp: 'leaseId',
    formatCarNumberRegularSpace,
    searchCars,
    searchSubjects
  }),
  computed: {
    saveFn () {
      return this.item.leaseId == null
        ? postLease
        : putLease
    },
    leaseType: {
      get () {
        return this.item.leaseType !== 0
      },
      set (v) {
        this.item.leaseType = v ? 1 : 0
      }
    }
  },
  watch: {
    show (val) {
      if (val) {
        this.item.leaseType == null && this.$set(this.item, 'leaseType', 0)
      }
    }
  }
}
</script>
