import DialogWrapper from '@/components/DialogWrapper'
import rules from '@/rules'

export default {
  components: { DialogWrapper },
  props: {
    show: { type: Boolean, default: false },
    value: { type: Object, default: () => ({}) }
  },
  data: () => ({
    item: {},
    rules,
    idProp: 'id',
    saving: false,
    valid: false
  }),
  watch: {
    show (val) {
      if (val) {
        this.item = JSON.parse(JSON.stringify(this.value))
        this.$refs.form.validate()
      }
    }
  },
  methods: {
    cancel () {
      this.$emit('cancel')
      this.$emit('show', false)
    },
    async save () {
      if (!this.saveFn) {
        return this.emitSave()
      }

      try {
        this.saving = true

        const res = await this.saveFn(JSON.parse(JSON.stringify(this.item)))
        if (typeof res === 'number') {
          this.item[this.idProp] = res
        } else if (res != null && res !== '') {
          this.item = res
        }

        this.$snotify.success(this.$t(`xhr-feedback.save.success`))
        this.emitSave()
      } catch (error) {
        if (await this.$confirm(this.$t('saving-failed-do-you-wanna-retry'))) {
          window.setTimeout(() => {
            this.save()
          })
        }
      } finally {
        this.saving = false
      }
    },
    emitSave () {
      if (this.$refs.form.validate()) {
        this.$emit('input', this.item)
        this.$emit('show', false)
      }
    }
  }
}
