<template>
  <v-container style="max-width: 40em;">
    <v-layout text-xs-center wrap>
      <v-flex xs12>
        <h1 class="display-2 font-weight-bold my-5" v-text="$t('logout')" />
      </v-flex>
      <v-flex xs12>
        <span v-text="error" />
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    error: '',
    timeout: null
  }),
  mounted () {
    this.$store.dispatch('logout')
    this.error = this.$t('logged-out')

    this.timeout = window.setTimeout(() => {
      this.$router.push({ name: 'login' })
    }, 10000)
  },
  beforeDestroy () {
    window.clearTimeout(this.timeout)
  }
}
</script>

