<template>
  <v-container style="max-width: 40em;">
    <v-layout text-xs-center wrap>
      <v-flex xs12>
        <h1 class="display-2 font-weight-bold my-5" v-text="$t('login')" />
      </v-flex>
      <v-flex xs12>
        <v-text-field v-model="email" :label="$t('email')" @keyup.enter="login" />
      </v-flex>
      <v-flex xs12>
        <v-text-field v-model="password" :label="$t('password')" type="password" @keyup.enter="login" />
      </v-flex>
      <v-flex xs12>
        <v-btn :loading="loading" block color="primary" @click="login">
          {{ $t('log-me-in') }}
        </v-btn>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { login } from '@/axios'

export default {
  data: () => ({
    email: null,
    password: null,
    loading: false
  }),
  methods: {
    async login () {
      try {
        this.loading = true

        const { token, roleId, userId } = await login(this.email, this.password)
        this.$store.dispatch('login', {
          token,
          role: roleId,
          user: userId
        })

        this.$router.push(
          this.$route.params.next
            ? { path: this.$route.params.next }
            : { name: 'home' }
        )
      } catch (error) {
        this.$snotify.error(this.$t('logging-in-failed'))
      } finally {
        this.loading = false
      }
    }
  }
}
</script>

