import { build } from './builder'
import { download } from './common'

const {
  search: searchRepairs,
  get: getRepairs,
  post: postRepair,
  put: putRepair,
  delete: deleteRepair
} = build('/repair', 'repairId')

async function getCarRepairs (carId, skip, count) {
  if (skip !== 0) {
    // Backend doesn't support paging.
    return []
  }

  const { data } = await download({
    method: 'get',
    url: `/repair/${carId}/repairs`
  })
  return data || []
}

export {
  searchRepairs, getRepairs, postRepair, putRepair, deleteRepair,
  getCarRepairs
}
