import { reCSharpKeys, download } from './common'

function build (urlPrefix, idProp) {
  return {
    async get (skip, count) {
      const { data } = await download({
        method: 'get',
        url: `${urlPrefix}/${skip}/${count}`
      })
      return data || []
    },

    async getOne (id) {
      const { data } = await download({
        method: 'get',
        url: `${urlPrefix}/${id}`
      })
      return data
    },

    async post (item) {
      const { data } = await download({
        method: 'post',
        url: `${urlPrefix}`,
        headers: {
          'content-type': 'application/json'
        },
        data: reCSharpKeys(item)
      })
      return data
    },

    async put (item) {
      const { data } = await download({
        method: 'post',
        url: `${urlPrefix}/${item[idProp]}`,
        headers: {
          'content-type': 'application/json'
        },
        data: reCSharpKeys(item)
      })
      return data
    },

    async delete (id) {
      return download({
        method: 'delete',
        url: `${urlPrefix}/${id}`
      })
    },

    async search (search, skip, count) {
      if (skip !== 0) {
        // Backend doesn't support paging.
        return []
      }

      const { data } = await download({
        method: 'get',
        url: search
          ? `${urlPrefix}/find/${encodeURIComponent(search)}`
          : `${urlPrefix}/${skip}/${count}`
      })
      return data || []
    }
  }
}

export { build }
