export default {
  data: () => ({
    idProp: null,
    rawActions: [],
    actionDialogs: {}
  }),
  computed: {
    actions () {
      const builder = {
        'open': {
          test: ({ name }) => this.$store.getters.isAllowed(name),
          build: ({ name }) => ({
            text: this.$t(name),
            exec: this.actionOpenFiltered.bind(null, name)
          })
        },
        'add': {
          test: ({ name }) => ['rw', 'ra'].includes(this.$store.getters.isAllowed(name)),
          build: ({ itemType, itemProp }) => {
            this.$set(this.actionDialogs, itemType, {
              item: {},
              show: false
            })
            return {
              text: this.$t(`add-${itemType}`),
              exec: this.actionAddNew.bind(null, itemType, itemProp)
            }
          }
        },
        'builder': {
          test: () => true,
          build: ({ builder }) => builder.apply(this)
        }
      }

      return [
        ...this.rawActions
          .filter(raw => builder[raw.type].test(raw))
          .map(raw => builder[raw.type].build(raw))
      ]
    }
  },
  methods: {
    actionOpenFiltered (name, item) {
      this.$router.push({
        name,
        query: {
          [this.idProp]: item[this.idProp]
        }
      })
    },
    actionAddNew (name, prop, item) {
      this.actionDialogs[name] = {
        item: {
          [prop]: item
        },
        show: true
      }
    }
  }
}
