<template>
  <v-form ref="form" v-model="valid">
    <DialogWrapper
      :headline="$t('revision')"
      :save-btn="valid"
      :saving="saving"
      :value="show"
      @cancel="cancel"
      @save="save"
    >
      <v-flex xs12>
        <RemoteSelect
          v-model="item.car"
          :label="$t('car')"
          :rules="[rules.required]"
          :search-items="searchCars"
          :format-text="formatCarNumberRegularSpace"
          item-text="name"
          item-value="carId"
        />
      </v-flex>
      <v-flex xs12>
        <DateTime v-model="item.lastRevisionDate" :label="$t('date')" :rules="[rules.required, rules.past]" />
      </v-flex>
      <v-flex xs12>
        <v-textarea v-model="item.description" :label="$t('description')" :rules="[rules.required]" />
      </v-flex>
    </DialogWrapper>
  </v-form>
</template>

<script>
import DateTime from '@/components/DateTime'
import DialogMixin from './DialogMixin'
import RemoteSelect from '@/components/RemoteSelect'
import { formatCarNumberRegularSpace } from '@/common'
import { postRevision, putRevision, searchCars } from '@/axios'

export default {
  components: { DateTime, RemoteSelect },
  mixins: [DialogMixin],
  data: () => ({
    idProp: 'revisionId',
    formatCarNumberRegularSpace,
    searchCars
  }),
  computed: {
    saveFn () {
      return this.item.revisionId == null
        ? postRevision
        : putRevision
    }
  }
}
</script>
