<template>
  <v-form ref="form" v-model="valid">
    <DialogWrapper
      :headline="$t('car')"
      :save-btn="valid"
      :saving="saving"
      :value="show"
      @cancel="cancel"
      @save="save"
    >
      <v-flex xs12>
        <v-text-field v-model="carNumber" :label="$t('car-number')" :rules="[rules.required, rules.carNumber]" />
      </v-flex>
      <v-flex xs12>
        <RemoteSelect
          v-model="item.owner"
          :label="$t('owner')"
          :rules="[rules.required]"
          :search-items="searchSubjects"
          item-text="name"
          item-value="subjectId"
        />
      </v-flex>
      <v-flex xs12>
        <RemoteSelect
          v-model="item.serie"
          :label="$t('serie')"
          :rules="[rules.required]"
          :search-items="searchSeries"
          item-text="serieId"
          item-value="serieId"
        />
      </v-flex>
      <v-flex xs12>
        <RemoteSelect
          v-model="item.goodGroup"
          :label="$t('good-group')"
          :rules="[rules.required]"
          :search-items="searchGoodGroups"
          item-text="name"
          item-value="goodGroupId"
        />
      </v-flex>
      <v-flex xs12>
        <RemoteSelect
          v-model="item.serviceResponsiblePerson"
          :label="$t('service-responsible-person')"
          :rules="[rules.required]"
          :search-items="searchSubjects"
          item-text="name"
          item-value="subjectId"
        />
      </v-flex>
      <v-flex xs12>
        <v-select v-model="item.state" :label="$t('state')" :items="states" :rules="[rules.required]" />
      </v-flex>
      <v-flex xs12>
        <v-switch v-model="item.certification" :label="$t('certification')" :rules="[rules.required]" />
      </v-flex>
      <v-flex xs12>
        <v-text-field v-model.number="item.revisionPeriod" :label="$t('revision-period')" :suffix="$t('units.days', [''])" :rules="[rules.required, rules.nonnegative, rules.integer]" type="number" />
      </v-flex>
      <v-flex xs12>
        <DateTime v-model="item.lastRevision" :label="$t('last-revision')" :rules="[rules.required, rules.past]" />
      </v-flex>
      <v-flex xs12>
        <DateTime v-model="item.lastZTE" :label="$t('last-ZTE')" :rules="[rules.required, rules.past]" />
      </v-flex>
      <v-flex xs12>
        <DateTime v-model="item.lastZTL" :label="$t('last-ZTL')" :rules="[rules.required, rules.past]" />
      </v-flex>
      <v-flex xs12>
        <v-text-field v-model="item.airBreakWeight" :label="$t('air-break-weight')" :rules="[rules.required, rules.nonnegative]" :suffix="$t('units.t', [''])" />
      </v-flex>
      <v-flex xs12>
        <v-text-field v-model="item.handBreakWeight" :label="$t('hand-break-weight')" :rules="[rules.required, rules.nonnegative]" :suffix="$t('units.t', [''])" />
      </v-flex>
    </DialogWrapper>
  </v-form>
</template>

<script>
import DateTime from '@/components/DateTime'
import DialogMixin from './DialogMixin'
import RemoteSelect from '@/components/RemoteSelect'
import { formatCarNumber } from '@/common'
import { postCar, putCar, searchGoodGroups, searchSubjects, searchSeries } from '@/axios'

export default {
  components: { DateTime, RemoteSelect },
  mixins: [DialogMixin],
  data: () => ({
    idProp: 'carId',
    searchGoodGroups,
    searchSeries,
    searchSubjects
  }),
  computed: {
    saveFn () {
      return this.item.carId == null
        ? postCar
        : putCar
    },
    carNumber: {
      get () {
        return formatCarNumber(this.item.name)
      },
      set (val) {
        this.$set(this.item, 'name', val.replace(/\D/g, ''))
      }
    },
    states () {
      return [0, 1, 2, 3].map(id => ({
        text: this.$t(`car-states.${id}`),
        value: id
      }))
    }
  },
  watch: {
    show (val) {
      if (val) {
        this.item.certification == null && this.$set(this.item, 'certification', false)
      }
    }
  }
}
</script>
