<template>
  <v-container pa-0>
    <v-layout v-if="value != null" wrap>
      <v-flex xs12>
        <v-timeline v-if="items.length" :dense="true" class="mx-3">
          <v-timeline-item
            v-for="({ changeDate, oldValue, newValue }, i) in items"
            :key="'timeline_item_' + i"
            color="primary"
            small
          >
            <v-layout pt-3>
              <v-flex xs4 d-flex style="align-items: center;">
                <div>
                  <strong>{{ $d(new Date(changeDate), 'date') }}</strong>
                  <br>
                  {{ $d(new Date(changeDate), 'time') }},
                  {{ $d(new Date(changeDate), 'weekday') }}
                </div>
              </v-flex>
              <v-flex xs8 d-flex style="align-items: center;">
                <dl>
                  <dt>{{ $t('new-value') }}</dt>
                  <dd>{{ newValue }}</dd>
                  <dt>{{ $t('old-value') }}</dt>
                  <dd>{{ oldValue }}</dd>
                </dl>
              </v-flex>
            </v-layout>
          </v-timeline-item>
        </v-timeline>
      </v-flex>
      <v-flex xs12>
        <v-btn :loading="loading" flat block color="primary" @click="load()">
          {{ $t(items.length ? 'load-more' : 'load-change-log') }}
        </v-btn>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { getCarHistory } from '@/axios'

export default {
  props: {
    itemsPerLoad: { type: Number, default: 3 },
    value: { type: Number, default: null }
  },
  data: () => ({
    items: [],
    loading: false
  }),
  watch: {
    value (v) {
      if (v != null) {
        this.load(true)
      }
    }
  },
  methods: {
    async load (clear) {
      this.loading = true
      if (clear) {
        this.items = []
      }
      try {
        const items = await getCarHistory(this.value, this.items.length, this.itemsPerLoad)
        if (items.length) {
          this.items.push(...items)
        }
      } catch (error) {
      } finally {
        this.loading = false
      }
    }
  }
}
</script>
