import Router from 'vue-router'
import Vue from 'vue'
import store from './store'

import About from './views/About.vue'
import Administration from './views/Administration.vue'
import AirBreaks from './views/AirBreaks.vue'
import Cars from './views/Cars.vue'
import Defects from './views/Defects.vue'
import GoodGroups from './views/GoodGroups.vue'
import HandBreaks from './views/HandBreaks.vue'
import History from './views/History.vue'
import Home from './views/Home.vue'
import Leases from './views/Leases.vue'
import Login from './views/Login.vue'
import Logout from './views/Logout.vue'
import Repairs from './views/Repairs.vue'
import Revisions from './views/Revisions.vue'
import Series from './views/Series.vue'
import Subjects from './views/Subjects.vue'

Vue.use(Router)

const router = new Router({
  routes: [{
    path: '/',
    redirect: '/login'
  }, {
    path: '/login/:next?',
    name: 'login',
    component: Login
  }, {
    path: '/logout',
    name: 'logout',
    component: Logout
  }, {
    path: '/home',
    name: 'home',
    component: Home
  }, {
    path: '/cars',
    name: 'cars',
    component: Cars
  }, {
    path: '/administration',
    name: 'administration',
    component: Administration
  }, {
    path: '/subjects',
    name: 'subjects',
    component: Subjects
  }, {
    path: '/air-breaks',
    name: 'air-breaks',
    component: AirBreaks
  }, {
    path: '/hand-breaks',
    name: 'hand-breaks',
    component: HandBreaks
  }, {
    path: '/series',
    name: 'series',
    component: Series
  }, {
    path: '/history',
    name: 'history',
    component: History
  }, {
    path: '/defects',
    name: 'defects',
    component: Defects
  }, {
    path: '/repairs',
    name: 'repairs',
    component: Repairs
  }, {
    path: '/good-groups',
    name: 'good-groups',
    component: GoodGroups
  }, {
    path: '/revisions',
    name: 'revisions',
    component: Revisions
  }, {
    path: '/leases',
    name: 'leases',
    component: Leases
  }, {
    path: '/about',
    name: 'about',
    component: About
    // // route level code-splitting
    // // this generates a separate chunk (about.[hash].js) for this route
    // // which is lazy-loaded when the route is visited.
    // component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
  }]
})

router.beforeEach(({ name, path }, _from, next) => {
  if (store.getters.isAllowed(name)) {
    next()
  } else {
    next({ name: 'login', params: { next: path } })
  }
})

export default router
