<template>
  <v-layout row wrap>
    <v-flex xs6>
      <v-menu
        v-model="menus.date"
        :close-on-content-click="false"
        lazy
        transition="scale-transition"
        offset-y
        full-width
      >
        <v-text-field
          slot="activator"
          v-model="date"
          :label="label"
          :error-messages="errors"
          readonly
        />
        <v-date-picker
          v-model="date"
          type="date"
        >
          <v-spacer />
          <v-btn flat @click="menus.date = false" v-text="$t('close')" />
        </v-date-picker>
      </v-menu>
    </v-flex>
    <v-flex xs6>
      <v-menu
        v-model="menus.time"
        :close-on-content-click="false"
        lazy
        transition="scale-transition"
        offset-y
        full-width
      >
        <v-text-field
          slot="activator"
          v-model="time"
          :label="$t('time')"
          :error-messages="errors"
          readonly
        />
        <v-time-picker
          v-model="time"
          format="24hr"
        >
          <v-spacer />
          <v-btn flat @click="menus.time = false" v-text="$t('close')" />
        </v-time-picker>
      </v-menu>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  props: {
    label: { type: String, default: null },
    rules: { type: Array, default: () => [] },
    value: { type: String, default: null }
  },
  data: () => ({
    date: null,
    time: null,
    seconds: null,
    menus: { date: false, time: false }
  }),
  computed: {
    item: {
      get () {
        if (this.date == null && this.time == null && this.seconds == null) {
          return null
        }

        const d = this.date != null && this.date.length === 10
          ? this.date
          : this.getDefaultValue().date
        const t = this.time != null && this.time.length === 5
          ? this.time
          : this.getDefaultValue().time
        const s = this.seconds != null && this.seconds.length === 2
          ? this.seconds
          : this.getDefaultValue().seconds

        return `${d}T${t}:${s}`
      },
      set (val) {
        if (val == null || val.length < 19) {
          this.date = null
          this.time = null
          this.seconds = null
        } else {
          this.date = val.slice(0, 10)
          this.time = val.slice(11, 16)
          this.seconds = val.slice(17, 19)
        }
      }
    },
    errors () {
      return this.rules
        .map(rule => rule(this.item))
        .filter(v => v !== true)
    }
  },
  watch: {
    value (val) {
      this.item = val
    },
    item (val) {
      if (this.date || this.time || this.seconds) {
        this.$emit('input', val)
      }
    }
  },
  mounted () {
    this.item = this.value
  },
  methods: {
    getDefaultValue () {
      const d = new Date()
      const str = new Date(d * 1 - d.getTimezoneOffset() * 60 * 1000)
        .toISOString()

      return {
        date: str.slice(0, 10),
        time: str.slice(11, 16),
        seconds: str.slice(17, 19)
      }
    }
  }
}
</script>
