<template>
  <v-dialog :value="value" :fullscreen="$vuetify.breakpoint.xsOnly" :persistent="!readOnly" scrollable max-width="600px" @keydown.esc="cancel()" @input="v => v === false ? cancel() : null">
    <v-card>
      <v-card-title>
        <span class="headline" v-text="headline" />
      </v-card-title>
      <v-card-text>
        <v-container grid-list-md>
          <v-layout wrap>
            <slot />
          </v-layout>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer />
        <v-btn
          color="primary"
          flat
          @click.native="cancel()"
        >
          {{ $t(readOnly ? 'close' : 'cancel') }}
        </v-btn>
        <v-btn
          v-if="!readOnly"
          :disabled="!saveBtn"
          :loading="saving"
          color="primary"
          flat
          @click.native="save()"
        >
          {{ $t('save') }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: {
    headline: { type: String, default: '' },
    readOnly: { type: Boolean, default: false },
    saveBtn: { type: Boolean, default: true },
    saving: { type: Boolean, default: false },
    value: { type: Boolean, default: false }
  },
  methods: {
    cancel () {
      this.$emit('input', false)
      this.$emit('cancel')
    },
    save () {
      this.$emit('input', false)
      this.$emit('save')
    }
  }
}
</script>
