<template>
  <ItemGrid :xhrs="xhrs" :new-item="newItem" :actions="actions" :id-prop="idProp" i18n-key="defect">
    <template slot="headline" slot-scope="{ item }">
      {{ item.name }}
    </template>
    <template slot-scope="{ item }">
      <DefectDL :item="item" />
    </template>
    <template slot="edit" slot-scope="{ item, itemCb, show, showCb }">
      <DefectDialog
        ref="dialog"
        :value="item" :show="show"
        @input="itemCb" @show="showCb"
      />
      <RepairDialog
        v-if="actionDialogs.repair"
        v-model="actionDialogs.repair.item"
        :show="actionDialogs.repair.show"
        @show="v => actionDialogs.repair.show = v"
      />
    </template>
  </ItemGrid>
</template>

<script>
import { searchDefects, getDefects, postDefect, putDefect, deleteDefect, getCarDefects } from '@/axios'

import ActionsBuilder from '@/mixins/ActionsBuilder'
import DefectDL from '@/components/itemDL/Defect'
import DefectDialog from '@/components/DefectDialog'
import ItemGrid from '@/components/ItemGrid'
import RepairDialog from '@/components/RepairDialog'

export default {
  components: { ItemGrid, DefectDL, DefectDialog, RepairDialog },
  mixins: [ActionsBuilder],
  data: () => ({
    getCarDefects,
    newItem: {},
    idProp: 'defectId',
    rawActions: [{
      type: 'add',
      name: 'repairs',
      itemType: 'repair',
      itemProp: 'defect'
    }]
  }),
  computed: {
    xhrs () {
      const carId = this.$route.query.carId
      if (carId != null) {
        return {
          searchItems: null,
          getItems: getCarDefects.bind(null, carId * 1),
          postItem: postDefect,
          putItem: putDefect,
          deleteItem: deleteDefect
        }
      } else {
        return {
          searchItems: searchDefects,
          getItems: getDefects,
          postItem: postDefect,
          putItem: putDefect,
          deleteItem: deleteDefect
        }
      }
    }
  }
}
</script>
