import Vue from 'vue'
import Vuex from 'vuex'
import acl from './acl'
import persistence from './persistence'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    role: null,
    token: null,
    userId: null
  },
  mutations: {
    setRole (state, role) {
      state.role = role
    },
    setToken (state, token) {
      state.token = token
    },
    setUserId (state, id) {
      state.userId = id
    }
  },
  actions: {
    login ({ commit }, { token, role, user }) {
      commit('setToken', token)
      commit('setRole', role)
      commit('setUserId', user)
    },
    logout ({ commit }) {
      commit('setToken', null)
      commit('setRole', null)
      commit('setUserId', null)
    }
  },
  getters: {
    isAllowed: state => {
      const rules = acl[state.role]
      if (rules === true) { // Superuser
        return name => name !== 'login' && 'rw'
      } else if (rules) { // Logged in user
        return name => rules[name]
      } else { // Others
        return name => acl.other[name]
      }
    }
  },
  plugins: [persistence.plugin]
})
