    >
      <v-flex xs12>
        <v-text-field v-model="item.name" :label="$t('name')" :rules="[rules.required]" />
      </v-flex>
      <v-flex xs12>
        <v-text-field v-model="item.email" :label="$t('email')" :rules="[rules.required, rules.email]" />
      </v-flex>
      <v-flex xs12>
        <v-text-field v-model="item.telephone" :label="$t('telephone')" :rules="[rules.required, rules.phoneNumber]" />
      </v-flex>
      <v-flex xs12>
