      </v-flex>
    </DialogWrapper>
  </v-form>
</template>

<script>
import DialogMixin from './DialogMixin'
import { postSubject, putSubject } from '@/axios'

export default {
  mixins: [DialogMixin],
  data: () => ({
    idProp: 'subjectId'
  }),
  computed: {
    saveFn () {
      return this.item.subjectId == null
        ? postSubject
        : putSubject
    }
  }
}
</script>
