<template>
  <ItemGrid :xhrs="xhrs" :new-item="newItem" :actions="actions" :id-prop="idProp" i18n-key="car">
    <template slot="headline" slot-scope="{ item }">
      {{ formatCarNumber(item.name) }}
    </template>
    <template slot-scope="{ item }">
      <CarDL :item="item" />
    </template>
    <template slot="edit" slot-scope="{ item, itemCb, show, showCb }">
      <CarDialog
        ref="dialog"
        :value="item" :show="show"
        @input="itemCb" @show="showCb"
      />
      <DefectDialog
        v-if="actionDialogs.defect"
        v-model="actionDialogs.defect.item"
        :show="actionDialogs.defect.show"
        @show="v => actionDialogs.defect.show = v"
      />
      <RevisionDialog
        v-if="actionDialogs.revision"
        v-model="actionDialogs.revision.item"
        :show="actionDialogs.revision.show"
        @show="v => actionDialogs.revision.show = v"
      />
      <LeaseDialog
        v-if="actionDialogs.lease"
        v-model="actionDialogs.lease.item"
        :show="actionDialogs.lease.show"
        @show="v => actionDialogs.lease.show = v"
      />
      <CarChangeLogDialog
        v-model="changeLog.item"
        :show="changeLog.show"
        @show="v => changeLog.show = v"
      />
    </template>
  </ItemGrid>
</template>

<script>
import { searchCars, getCars, postCar, putCar, deleteCar } from '@/axios'

import ActionsBuilder from '@/mixins/ActionsBuilder'
import CarChangeLogDialog from '@/components/CarChangeLogDialog'
import CarDL from '@/components/itemDL/Car'
import CarDialog from '@/components/CarDialog'
import DefectDialog from '@/components/DefectDialog'
import ItemGrid from '@/components/ItemGrid'
import LeaseDialog from '@/components/LeaseDialog'
import RevisionDialog from '@/components/RevisionDialog'
import { formatCarNumber } from '@/common'

export default {
  components: { ItemGrid, CarChangeLogDialog, CarDL, CarDialog, DefectDialog, LeaseDialog, RevisionDialog },
  mixins: [ActionsBuilder],
  data: () => ({
    formatCarNumber,
    newItem: {},
    idProp: 'carId',
    changeLog: {
      item: undefined,
      show: false
    },
    rawActions: [{
      type: 'open',
      name: 'defects'
    }, {
      type: 'open',
      name: 'repairs'
    }, {
      type: 'open',
      name: 'revisions'
    }, {
      type: 'add',
      name: 'defects',
      itemType: 'defect',
      itemProp: 'car'
    }, {
      type: 'add',
      name: 'revisions',
      itemType: 'revision',
      itemProp: 'car'
    }, {
      type: 'add',
      name: 'leases',
      itemType: 'lease',
      itemProp: 'car'
    }, {
      type: 'builder',
      builder () {
        return {
          text: this.$t('change-log'),
          exec: this.openChangeLog
        }
      }
    }],
    xhrs: {
      searchItems: searchCars,
      getItems: getCars,
      postItem: postCar,
      putItem: putCar,
      deleteItem: deleteCar
    }
  }),
  methods: {
    openChangeLog (item) {
      this.changeLog = {
        item,
        show: true
      }
    }
  }
}
</script>
