<template>
  <v-form ref="form" v-model="valid">
    <DialogWrapper
      :headline="$t('repair')"
      :save-btn="valid"
      :saving="saving"
      :value="show"
      @cancel="cancel"
      @save="save"
    >
      <v-flex xs12>
        <RemoteSelect
          v-model="item.defect"
          :label="$t('defect')"
          :rules="[rules.required]"
          :search-items="searchDefects"
          item-text="name"
          item-value="defectId"
        />
      </v-flex>
      <v-flex xs12>
        <DateTime v-model="item.repairDate" :label="$t('date')" :rules="[rules.required, rules.past]" />
      </v-flex>
      <v-flex xs12>
        <v-textarea v-model="item.description" :label="$t('description')" :rules="[rules.required]" />
      </v-flex>
    </DialogWrapper>
  </v-form>
</template>

<script>
import DateTime from '@/components/DateTime'
import DialogMixin from './DialogMixin'
import RemoteSelect from '@/components/RemoteSelect'
import { postRepair, putRepair, searchDefects } from '@/axios'

export default {
  components: { DateTime, RemoteSelect },
  mixins: [DialogMixin],
  data: () => ({
    idProp: 'repairId',
    searchDefects
  }),
  computed: {
    saveFn () {
      return this.item.repairId == null
        ? postRepair
        : putRepair
    }
  }
}
</script>
