<template>
  <v-container>
    <v-layout text-xs-center wrap>
      <v-flex xs12>
        <h1 class="display-2 font-weight-bold my-5" v-text="$t('welcome')" />
      </v-flex>
      <v-flex xs12 sm6 xl3>
        <v-card class="ma-2">
          <v-card-title class="headline">
            {{ $t('logged-user') }}
          </v-card-title>
          <v-card-text class="text-xs-left">
            <div v-if="!user" class="text-xs-center py-5">
              <v-progress-circular :indeterminate="true" color="primary" />
            </div>
            <template v-else>
              <UserDL :item="user" :show-name="true" />
              <dl>
                <dt>{{ $t('remaining-time') }}</dt>
                <dd>{{ $t('units.time-hms', [hours, minutes, seconds]) }}</dd>
              </dl>
            </template>
          </v-card-text>
        </v-card>
      </v-flex>
      <v-flex xs12 sm6 xl9>
        <v-layout wrap>
          <v-flex v-if="cars !== false" xs12 xl4>
            <transition name="fade" mode="out-in">
              <v-layout row wrap>
                <v-flex v-if="!cars" xs12 py-5>
                  <v-progress-circular :indeterminate="true" color="primary" />
                </v-flex>
                <v-flex v-else-if="!cars.length" xs12>
                  <v-card class="ma-2">
                    <v-card-title class="headline">
                      {{ $t('car') }}
                    </v-card-title>
                    <v-card-text>
                      {{ $t('no-items') }}
                    </v-card-text>
                  </v-card>
                </v-flex>
                <v-flex v-for="(item, i) in cars" v-else :key="`home_car_${i}`" xs12>
                  <v-card class="ma-2">
                    <v-card-title class="headline">
                      {{ $t('car') }}
                    </v-card-title>
                    <v-card-text class="text-xs-left">
                      <CarDL :item="item" :show-name="true" />
                    </v-card-text>
                  </v-card>
                </v-flex>
              </v-layout>
            </transition>
          </v-flex>
          <v-flex v-if="defects !== false" xs12 xl4>
            <transition name="fade" mode="out-in">
              <v-layout row wrap>
                <v-flex v-if="!defects" xs12 py-5>
                  <v-progress-circular :indeterminate="true" color="primary" />
                </v-flex>
                <v-flex v-else-if="!defects.length" xs12>
                  <v-card class="ma-2">
                    <v-card-title class="headline">
                      {{ $t('defect') }}
                    </v-card-title>
                    <v-card-text>
                      {{ $t('no-items') }}
                    </v-card-text>
                  </v-card>
                </v-flex>
                <v-flex v-for="(item, i) in defects" v-else :key="`home_defect_${i}`" xs12>
                  <v-card class="ma-2">
                    <v-card-title class="headline">
                      {{ $t('defect') }}
                    </v-card-title>
                    <v-card-text class="text-xs-left">
                      <DefectDL :item="item" :show-name="true" />
                    </v-card-text>
                  </v-card>
                </v-flex>
              </v-layout>
            </transition>
          </v-flex>
          <v-flex v-if="leases !== false" xs12 xl4>
            <transition name="fade" mode="out-in">
              <v-layout row wrap>
                <v-flex v-if="!leases" xs12 py-5>
                  <v-progress-circular :indeterminate="true" color="primary" />
                </v-flex>
                <v-flex v-else-if="!leases.length" xs12>
                  <v-card class="ma-2">
                    <v-card-title class="headline">
                      {{ $t('lease') }}
                    </v-card-title>
                    <v-card-text>
                      {{ $t('no-items') }}
                    </v-card-text>
                  </v-card>
                </v-flex>
                <v-flex v-for="(item, i) in leases" v-else :key="`home_lease_${i}`" xs12>
                  <v-card class="ma-2">
                    <v-card-title class="headline">
                      {{ $t('lease') }}
                    </v-card-title>
                    <v-card-text class="text-xs-left">
                      <LeaseDL :item="item" :show-name="true" />
                    </v-card-text>
                  </v-card>
                </v-flex>
              </v-layout>
            </transition>
          </v-flex>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { getCarDashboard, getDefectDashboard, getLeaseDashboard, getUser } from '@/axios'

import CarDL from '@/components/itemDL/Car'
import DefectDL from '@/components/itemDL/Defect'
import LeaseDL from '@/components/itemDL/Lease'
import UserDL from '@/components/itemDL/User'

export default{
  components: { CarDL, DefectDL, LeaseDL, UserDL },
  data: () => ({
    cars: null,
    defects: null,
    leases: null,
    remainingTime: 0,
    updateInterval: null,
    user: null
  }),
  computed: {
    hours () {
      return Math.floor(this.remainingTime / 60 / 60)
    },
    minutes () {
      return Math.floor(this.remainingTime / 60 % 60)
    },
    seconds () {
      return Math.floor(this.remainingTime % 60)
    }
  },
  mounted () {
    if (this.$store.getters.isAllowed('cars')) {
      getCarDashboard().then(cars => {
        this.cars = cars
      }, () => {
        this.cars = []
      })
    } else {
      this.cars = false
    }

    if (this.$store.getters.isAllowed('defects')) {
      getDefectDashboard().then(defects => {
        this.defects = defects
      }, () => {
        this.defects = []
      })
    } else {
      this.defects = false
    }

    if (this.$store.getters.isAllowed('leases')) {
      getLeaseDashboard().then(leases => {
        this.leases = leases
      }, () => {
        this.leases = []
      })
    } else {
      this.leases = false
    }

    getUser(this.$store.state.userId).then(user => {
      this.user = user

      const end = new Date(this.user.tokenValidTo) * 1
      const update = () => {
        this.remainingTime = (end - Date.now()) / 1000
      }
      this.updateInterval = window.setInterval(update, 1000)
      update()
    }, () => {})
  },
  beforeDestroy () {
    window.clearInterval(this.updateInterval)
  }
}
</script>
