<template>
  <ItemGrid :xhrs="xhrs" :new-item="newItem" i18n-key="repair" id-prop="repairId">
    <template slot="headline" slot-scope="{ item }">
      {{ item.defect.name }}
    </template>
    <template slot-scope="{ item }">
      <RepairDL :item="item" />
    </template>
    <template slot="edit" slot-scope="{ item, itemCb, show, showCb }">
      <RepairDialog
        ref="dialog"
        :value="item" :show="show"
        @input="itemCb" @show="showCb"
      />
    </template>
  </ItemGrid>
</template>

<script>
import { searchRepairs, getRepairs, postRepair, putRepair, deleteRepair, getCarRepairs } from '@/axios'

import ItemGrid from '@/components/ItemGrid'
import RepairDL from '@/components/itemDL/Repair'
import RepairDialog from '@/components/RepairDialog'

export default {
  components: { ItemGrid, RepairDL, RepairDialog },
  data: () => ({
    newItem: {}
  }),
  computed: {
    xhrs () {
      const carId = this.$route.query.carId
      if (carId != null) {
        return {
          searchItems: null,
          getItems: getCarRepairs.bind(null, carId * 1),
          postItem: postRepair,
          putItem: putRepair,
          deleteItem: deleteRepair
        }
      } else {
        return {
          searchItems: searchRepairs,
          getItems: getRepairs,
          postItem: postRepair,
          putItem: putRepair,
          deleteItem: deleteRepair
        }
      }
    }
  }
}
</script>
