<template>
  <v-form ref="form" v-model="valid">
    <DialogWrapper
      :headline="$t('hand-break')"
      :save-btn="valid"
      :saving="saving"
      :value="show"
      @cancel="cancel"
      @save="save"
    >
      <v-flex xs12>
        <v-text-field v-model="item.name" :label="$t('name')" :rules="[rules.required]" />
      </v-flex>
    </DialogWrapper>
  </v-form>
</template>

<script>
import DialogMixin from './DialogMixin'
import { postHandBreak, putHandBreak } from '@/axios'

export default {
  mixins: [DialogMixin],
  data: () => ({
    idProp: 'handBreakId'
  }),
  computed: {
    saveFn () {
      return this.item.handBreakId == null
        ? postHandBreak
        : putHandBreak
    }
  }
}
</script>
