import VuexPersistence from 'vuex-persist'

const persistedMutations = {
  setRole: true,
  setToken: true,
  setUserId: true
}

const persistence = new VuexPersistence({
  key: 'KST/INPRO',
  storage: window.localStorage,
  reducer: state => ({
    role: state.role,
    token: state.token,
    userId: state.userId
  }),
  filter: mutation => persistedMutations[mutation.type]
})

export default persistence
