import Vue from 'vue'
import VueI18n from 'vue-i18n'

Vue.use(VueI18n)

function loadLocales () {
  const locales = require.context('./locales', true, /\.(json|yml)$/i)
  const options = {}
  locales.keys().forEach(key => {
    const matched = key.match(/(^|\/)([a-z0-9_-]+)\.([a-z0-9_-]+)\.(json|yml)$/i)
    if (matched && matched.length === 5) {
      const locale = matched[2]
      const type = matched[3]

      options[type] || (options[type] = {})
      options[type][locale] = locales(key)
    }
  })
  return options
}

export default new VueI18n({
  locale: process.env.VUE_APP_I18N_LOCALE || 'en',
  fallbackLocale: process.env.VUE_APP_I18N_FALLBACK_LOCALE || 'en',
  ...loadLocales()
})
