import { build } from './builder'
import { download } from './common'

const {
  search: searchDefects,
  get: getDefects,
  post: postDefect,
  put: putDefect,
  delete: deleteDefect
} = build('/fault', 'defectId')

async function getCarDefects (carId, skip, count) {
  if (skip !== 0) {
    // Backend doesn't support paging.
    return []
  }

  const { data } = await download({
    method: 'get',
    url: `/fault/carfaults/${carId}`
  })
  return data || []
}

async function getDefectDashboard () {
  const { data } = await download({
    method: 'get',
    url: `/fault/dashboard`
  })
  return data || []
}

export {
  searchDefects, getDefects, postDefect, putDefect, deleteDefect,
  getDefectDashboard, getCarDefects
}
