<template>
  <v-form ref="form" v-model="valid">
    <DialogWrapper
      :headline="$t('user')"
      :save-btn="valid"
      :saving="saving"
      :value="show"
      @cancel="cancel"
      @save="save"
    >
      <v-flex xs12 md6>
        <v-text-field v-model="item.firstName" :label="$t('first-name')" :rules="[rules.required]" />
      </v-flex>
      <v-flex xs12 md6>
        <v-text-field v-model="item.lastName" :label="$t('last-name')" :rules="[rules.required]" />
      </v-flex>
      <v-flex xs12>
        <v-text-field v-model="item.email" :label="$t('email')" :rules="[rules.required, rules.email]" />
      </v-flex>
      <v-flex xs12>
        <v-text-field v-model="item.password" :label="$t('password')" :rules="[rules.minLength(8)]" type="password" />
      </v-flex>
      <v-flex xs12>
        <v-select v-model="item.role.roleId" :label="$t('role')" :items="roles" :rules="[rules.required]" />
      </v-flex>
    </DialogWrapper>
  </v-form>
</template>

<script>
import DialogMixin from './DialogMixin'
import { postUser, putUser } from '@/axios'

export default {
  mixins: [DialogMixin],
  data: () => ({
    idProp: 'userId',
    item: {
      role: {}
    }
  }),
  computed: {
    saveFn () {
      return this.item.userId == null
        ? postUser
        : putUser
    },
    roles () {
      return [1, 2, 3, 4].map(roleId => ({
        text: this.$t(`roles.${roleId}`),
        value: roleId
      }))
    }
  },
  methods: {
    save () {
      this.item.role.name = this.$t(`roles.${this.item.role.roleId}`)
      DialogMixin.methods.save.call(this)
    }
  }
}
</script>
