import { build } from './builder'
import { download, reCSharpKeys } from './common'

const {
  search: searchLeases,
  get: getLeases,
  // post: postLease,
  // put: putLease,
  delete: deleteLease
} = build('/rent', 'leaseId')

async function getLeaseDashboard () {
  const { data } = await download({
    method: 'get',
    url: `/rent/dashboard`
  })
  return data || []
}

async function putLease (item) {
  const { data } = await download({
    method: 'post',
    url: item.leaseType === 0 ? `/rent/rentfrom` : `/rent/rentto`,
    headers: {
      'content-type': 'application/json'
    },
    data: reCSharpKeys({
      carId: item.car.carId,
      dueDate: item.endDate,
      subjectId: item.subject.subjectId
    })
  })
  return data
}
const postLease = putLease

export {
  searchLeases, getLeases, postLease, putLease, deleteLease,
  getLeaseDashboard
}
